.MenuIcons {
	padding: 0;
	margin: 0;
	position: relative;
	width: 134px;
	height: 110px;
	/*overflow: hidden;*/
	background-color: #fff;
	/*box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);*/
}
.MenuIcons__div_for_circle {
	width: 100%; 
	height: 100%;
	overflow: hidden;
	position: absolute;
	/*box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);*/
}
.MenuIcons__div_for_circle::after {
	content: '';
	position: absolute;
	left: 57px;
	top: 38px;
	border-radius: 50%;
	width: 145px;
	height: 135px;
	box-sizing: border-box;
	background-color: #fff;
	z-index: 10;
	box-shadow: inset 0px 4px 10px rgba(0, 0, 0, 0.1);
}
.MenuIcons__div_for_shadow {
	position: absolute;
	width: 143px; 
	height: 200px;
	top: -67px;
	left: -27px;
	overflow: hidden; 
	transform: rotate(45deg);
}
.MenuIcons__div_for_shadow::after {
	content: '';
	position: absolute;
	width: 134px;
	height: 110px;
	top: 44px;
	left: 35px;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
	transform: rotate(-45deg);
}
.MenuIcons li {
	position: absolute;
	text-decoration: none;
	list-style: none;
	z-index: 2;
	transition: background-color 0.25s;
}
.MenuIcons li:hover {
	background-color: #FFE073;
}
.MenuIcons__startup {
	top: 60px;
	height: 50px;
	width: 80px;
}
.MenuIcons__startup img {
	width: 33px;
	margin-left: 16px;
	margin-top: 7px;
}
.MenuIcons__people {
	width: 75px;
	height: 60px;
	border-right: 1px solid rgba(15, 85, 128, 0.5);
	border-bottom: 1px solid rgba(15, 85, 128, 0.5);
}
.MenuIcons__people img {
	width: 33px;
	margin-top: 13px;
	margin-left: 16px;
}
.MenuIcons__find {
	left: 75px;
	width: 59px;
	height: 60px;
}
.MenuIcons__find img {
	width: 25px;
	height: 25px;
	margin-top: 10px;
	margin-left: 11px;
}