body {
	padding: 0;
	margin: 0;
}
p, ul {
	margin: 0;
}
.Container {
	width: 745px;
	margin-left: auto;
	margin-right: auto;
}

.Header {
	height: 50px;
	background-color: #FFE073;
	position: relative;
}
.Header .Container {
	position: relative;
	height: 100%;
}

.Header__title {
	display: inline-block;
	font-family: Fira Sans;
	font-style: italic;
	font-weight: 500;
	line-height: 50px;
	font-size: 24px;
	color: #254E5B;
}
.Header__button_exit {
	position: relative;
	width: 28px;
	height: 31px;
	top: 50%;
	float: right;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	background-color: #CAD79F;
	border-radius: 5px;
	cursor: pointer;
	transition: background-color 0.3s;
}
.Header__button_exit:hover {
	background-color: #6AD0C8;
}
.Header__button_exit img {
	width: 24px;
	height: 26px;
	margin-top: 2px;
	margin-left: 2px;
}
.Body {
	padding-top: 12px;
}

.Body .SearchJoinLine {
	position: relative;
	display: inline-block;
	margin-left: -30px;
	z-index: 14;
}
.Body .MenuIcons {
	display: inline-block;
}
.MenuIcons {
	padding: 0;
	margin: 0;
	position: relative;
	width: 134px;
	height: 110px;
	/*overflow: hidden;*/
	background-color: #fff;
	/*box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);*/
}
.MenuIcons__div_for_circle {
	width: 100%; 
	height: 100%;
	overflow: hidden;
	position: absolute;
	/*box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);*/
}
.MenuIcons__div_for_circle::after {
	content: '';
	position: absolute;
	left: 57px;
	top: 38px;
	border-radius: 50%;
	width: 145px;
	height: 135px;
	box-sizing: border-box;
	background-color: #fff;
	z-index: 10;
	box-shadow: inset 0px 4px 10px rgba(0, 0, 0, 0.1);
}
.MenuIcons__div_for_shadow {
	position: absolute;
	width: 143px; 
	height: 200px;
	top: -67px;
	left: -27px;
	overflow: hidden; 
	-webkit-transform: rotate(45deg); 
	        transform: rotate(45deg);
}
.MenuIcons__div_for_shadow::after {
	content: '';
	position: absolute;
	width: 134px;
	height: 110px;
	top: 44px;
	left: 35px;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}
.MenuIcons li {
	position: absolute;
	text-decoration: none;
	list-style: none;
	z-index: 2;
	transition: background-color 0.25s;
}
.MenuIcons li:hover {
	background-color: #FFE073;
}
.MenuIcons__startup {
	top: 60px;
	height: 50px;
	width: 80px;
}
.MenuIcons__startup img {
	width: 33px;
	margin-left: 16px;
	margin-top: 7px;
}
.MenuIcons__people {
	width: 75px;
	height: 60px;
	border-right: 1px solid rgba(15, 85, 128, 0.5);
	border-bottom: 1px solid rgba(15, 85, 128, 0.5);
}
.MenuIcons__people img {
	width: 33px;
	margin-top: 13px;
	margin-left: 16px;
}
.MenuIcons__find {
	left: 75px;
	width: 59px;
	height: 60px;
}
.MenuIcons__find img {
	width: 25px;
	height: 25px;
	margin-top: 10px;
	margin-left: 11px;
}
.LineOFConditions {
	margin-top: 20px;
	margin-bottom: 8px;
}
.SearchJoinLine {
	display: inline-block;
}
.SearchField {
	display: inline-block;
	vertical-align: middle;
}
.SearchGamburger {
	display: inline-block;
	margin-left: 20px;
	vertical-align: middle;
}
.SearchButton {
	display: inline-block;
	margin-left: 16px;
	vertical-align: middle;
}
.SearchField {
	position: relative;
	width: 431px;
	height: 30px;
	background-color: #FFE073;
}
.SearchField__input {
	position: relative;
	width: 96%;
	height: 80%;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	border: none;
	padding: 0;
	padding-left: 10px;
	background-color: #fff;
	text-decoration: none;
}
.SearchField__input:focus, .SearchField__input:hover {
	outline: none;
	background-color: #F3F3F3;
}

.SearchField__div_white1 {
	position: absolute;
	top: 4px;
	left: 0;
	width: 5px;
	height: 11px;
	background-color: #fff;
}
.SearchField__div_white2 {
	position: absolute;
	right: 0;
	bottom: 4px;
	width: 5px;
	height: 11px;
	background-color: #fff;
}
.SearchGamburger {
	width: 31px;
	height: 25px;
	background: #F3F3F3;
	cursor: pointer;
}
.SearchGamburger div {
	display: block;
	margin-top: 6px;
	margin-left: auto;
	margin-right: auto;
	width: 23px;
	height: 2px;
	background: #84BEBA;
}
.SearchGamburger div:nth-child(1) {
	margin-top: 4px;
}
.SearchButton {
	width: 85px;
	height: 30px;
	padding-left: 11px;
	background: linear-gradient(139.09deg, #FFE073 45.91%, #66B489 221.28%);
	cursor: pointer;

	font-family: Fira Sans;
	font-style: italic;
	font-weight: 500;
	line-height: 30px;
	font-size: 16px;
	color: #254E5B;
}

.LineOFConditions {
	display: block;
	height: 10px;
	background-color: #F3F3F3;
	transition: height 1s;
}

.LineOFConditions_show_yes {
	height: 100px;
}
.StartupCard {
	margin-right: 36px;
	margin-bottom: 49px;
}
.Startup__popup {
	right: -105%;
	top: -50%;
	display: none;
	transition: opacity 0.1s ease;
	z-index: 20;
}
.StartupCard:hover .Startup__popup {
	display: block;
}

.StartupCard {
	position: relative;
	display: inline-block;
	width: 208px;
	box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.25);
}
.StartupCard__topLine {
	height: 7px;
	background-color: #84BEBA;
}
.StartupCard__title {
	min-height: 33px;
	padding-left: 7px;
	overflow-x: hidden;

	white-space: nowrap;
	text-overflow: ellipsis;
	font-family: Fira Sans;
	font-style: italic;
	font-weight: 500;
	line-height: 33px;
	font-size: 14px;
	color: #111F24;
}
.StartupCard__body {
	padding-left: 7px;
	padding-top: 9px;
	padding-right: 30px;
	padding-bottom: 20px;
	background-color: #F3F3F3;
	font-family: Fira Sans;
	font-style: italic;
	font-weight: 500;
	line-height: normal;
	font-size: 14px;
	color: #224A55;
}
.StartupCard__bottomLine {
	height: 7px;
	background-color: #009D90;
}

.StartupCard__triangle {
	position: absolute;
	right: 0;
	bottom: 0;
}
.StartupCard__button {
	position: absolute;
	cursor: pointer;
	height: 41px;
	width: 91px;
	right: -18px;
	bottom: -10px;
	background: linear-gradient(145.33deg, #FFE485 14.26%, #009D90 431.65%);
	background-color: #F4E185;
	box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.25);
	transition: background-color 1.35s;

	padding-left: 5px;
	font-family: Fira Sans;
	font-style: italic;
	font-weight: 500;
	line-height: 41px;
	font-size: 16px;
	color: #254E5B;
}
.StartupCard__button:hover {
	background: linear-gradient(145.33deg, rgba(255, 228, 133, 0.01) 14.26%, rgba(0, 157, 144, 0.01) 431.65%);
	background-color: #FFE485;
}
.Startup__popup {
	position: absolute;
}
.popup__main-div {
	position: relative;
	min-height: 80px;
	min-width: 200px;
	padding: 12px 15px;
	background-color: #DDF8F6;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);

	font-family: Fira Sans;
	font-style: italic;
	font-weight: 500;
	line-height: normal;
	font-size: 14px;
	color: #17323B;
	z-index: 11;
}
.popup__back-div {
	position: absolute;
	left: -10px;
	bottom: -7px;
	width: 65px;
	height: 48px;

	background: #F8E08B;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
	z-index: 10;
}


.popup__author {
	margin-bottom: 10px;
}
.popup__author p {
	display: inline-block;
	margin-right: 25px;
}
.popup__author img {
	width: 30px;
	height: 30px;
}
.popup__author-name {
	display: inline-block;
	vertical-align: top;	
}
